import * as React from 'react';
import { Page } from 'tonva';
import marked from 'marked';
import logo from '../images/logo.png';

const content = `<p>

200,000 </p>
<p>J&KAmethystJ&K Scientific 
30,000 
600,000 

</p>
<p>


</p>
`

export class About extends React.Component {
    render() {
        let right = null;
        return <Page header="" right={right}>
            <div className='bg-white p-3'>
                <img className="h-3c position-absolute" src={logo} alt="" />
                <div className="h3 flex-fill text-center">
                    <span className="text-primary mr-3"></span>
                </div>
                <div className="mt-5" dangerouslySetInnerHTML={{ __html: marked(content) }} />
            </div>
        </Page>;
    }
}